.card-search{

    box-shadow: none;
    display: flex;
    justify-content: left;
    margin-left: 10%;
}

.search-content{

    font-family: 'Courier New', Courier, monospace;
    text-align: center;
    font-size: 20px;
}

.search-bar{

    background: rgb(245, 226, 214);
    width: 50%;

}


.info-text {
    font-family: 'Courier New', Courier, monospace;
    text-align: left;
    height: 30px;
    font-weight: bold;
}

.imc-barra {
    height: 50%;
}

.card-center{

    box-shadow: none;
    display: flex;
    justify-content: left;
    margin-left: 10%;
}

.card-left{

    box-shadow: none;
    display: flex;
    justify-content: left;
    margin-left: 5%;
    padding: 2.5%;
}

.layout-selecao-mails{

    margin-left: 10%;
    margin-right: 10%;
}

.distancia-horizontal{

    margin-left: 2.5%;
}

.grid-avaliacao{

    margin-left: 10%;
    margin-right: 10%;
    justify-content: center;
    --ion-grid-padding: 0;
}

.background-orange{

    background: rgb(245, 226, 214);
}

.espaco-vertical-search{

    margin-bottom: 2.5%;
}

.comprimento-lista-mails{


    height: 175px;
}

.descricao-avaliacao{

    width: 85%;
}

.separador{

    margin-top: 2.5%;
    border: 1px solid rgb(246, 199, 167);
    margin-bottom: 2.5%;
    margin-left: 5%;
    margin-right: 5%;
 }

.botao{

    margin-top: 15%;
    margin-bottom: 10%;
}

.card{
    background: rgb(245, 226, 214);
    padding: 1.5%;
    border: 1px solid rgb(246, 199, 167);
}

.grid{

    margin-left: 5%;
    width: 90%;
}

.cardColSelected{
    position: relative;
    margin: 10px auto !important;
   --ion-background-color: white;
   border: 4px outset rgb(204, 107, 69);

}

.cardColUnselected{
    position: relative;
    margin: 10px auto !important;
    --ion-background-color : white;
}

.responsiveTitleCAval{

    font-family: 'Courier New', Courier, monospace;
    font-size: 20px;
}

.responsiveProfilePicCAval {
    max-width: 150px;
    max-height: 150px;
}

.responsiveTextCardCavl {
    font-size: 18px;
    font-family: 'Courier New', Courier, monospace
}

.seccaoCAval {
    font-size: 18px;
    font-weight: bold;
    color: black;
}

.botaoCAval {
    width: 48%;
}

@media screen and (max-width: 600px) {
    .responsiveTitleCAval {
        font-size: 15px;
    }
    .aulaImage {
        width: 100px;
        height: 100px;
    }

    .responsiveProfilePicCAval {
        max-width: 70px;
        max-height: 70px;
    }
    .responsiveTextCardCavl {
        font-size: 12px;
    }
    .seccaoCAval {
        font-size: 12px;
        font-weight: bold;
        color: black;
    }
    
    .botaoCAval {
        width: 48%;
    }

}